@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yeseva+One&display=swap);
:root {
  --s-clr: #efa765;
  --n-clr: #fff;
  --h-clr: #000;
  --bg-dark-clr: #1d1922;
  --bg-light-clr: #2d2d38;
  --light-clr: #b8b8be;
}

body {
  background: #000;
  margin: 0;
  padding: 0;
  font-family: "Varela Round";
}

nav {
  width: 94%;
  display: grid;
  grid-template-columns: 1fr 1fr 3fr 1fr 1fr;
  grid-template-areas: "logo . ul . btn";
  margin: 0 auto;
  padding: 25px 0;
  align-items: center;
  height: 3.5rem;
}

nav ul {
  display: flex;
  justify-content: center;
  padding: 0;
  grid-area: ul;
  transition: all 0.2s ease;
}

nav ul li {
  margin: 0;
  height: 100%;
  padding: 0 20px;
  list-style-type: none;
  text-transform: uppercase;
  font-size: 0.9rem;
  transition: all 0.2s ease;
  cursor: pointer;
  color: #fff;
  color: var(--n-clr);
}

ul li:hover {
  color: #efa765;
  color: var(--s-clr);
}

.active {
  color: #efa765;
  color: var(--s-clr);
}

nav a {
  text-decoration: none;
  color: #fff;
  color: var(--n-clr);
  cursor: pointer;
  font-size: 1.8rem;
  font-family: "Yeseva One";
  grid-area: logo;
  transition: all 0.2s ease;
}

nav a:hover {
  color: #efa765;
  color: var(--s-clr);
}

.btn {
  grid-area: btn;
  height: 100%;
  border: 2px solid #fff;
  border: 2px solid var(--n-clr);
  background: none;
  cursor: pointer;
  color: #fff;
  color: var(--n-clr);
  text-transform: uppercase;
  transition: all 0.2s ease;
  letter-spacing: 1.2px;
  border-radius: 0;
}

nav .btn {
  max-width: 180px;
}

.btn:hover {
  background: #fff;
  background: var(--n-clr);
  color: #000;
  color: var(--h-clr);
}

.bar-btn {
  grid-area: bar;
  display: none;
  justify-self: end;
  padding: 15px 30px;
  border: none;
}

.bar-btn .icon {
  color: inherit;
}

.bar-btn:hover .icon {
  color: #efa765;
  color: var(--s-clr);
}

.close {
  display: none;
}

@media screen and (max-width: 1024px) {
  nav {
    grid-template-columns: 1fr 3fr 1fr;
    grid-template-areas: "logo ul btn";
  }
}

@media screen and (max-width: 920px) {
  nav {
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "logo ul";
  }
  nav button {
    display: none;
  }
}

@media screen and (max-width: 700px) {
  .bar-btn {
    display: block;
  }
  nav {
    grid-template-areas: "logo logo bar" "ul ul ul";
    grid-template-columns: 1fr 0.5fr 1fr;
  }
  nav a {
    justify-self: start;
  }
  nav ul {
    justify-content: flex-start;
    flex-direction: column;
    position: fixed;
    z-index: 1;
    width: 0%;
    max-width: 90%;
    height: 100%;
    overflow: hidden;
    background: #000;
    margin: 0;
    top: 0;
    right: 0;
  }
  .close {
    display: block;
    position: relative;
    width: 50px;
    height: 50px;
    top: 0;
    align-self: flex-end;
    background: none;
    border: none;
    cursor: pointer;
  }
  .cls {
    color: #fff;
    font-size: 25px;
    transition: all 0.2s ease;
  }
  nav ul li {
    padding: 30px;
    font-size: 1.1rem;
    color: #fff;
    max-height: 81px;
    box-sizing: border-box;
    border: 1px solid #2d2d38;
    border: 1px solid var(--bg-light-clr);
    border-left: none;
    border-right: none;
  }
}

:root {
  --back-img: url(/steakhouse/static/media/contact.5153ca7b.jpg);
}

html {
  height: 100%;
}

body:before {
  content: "";
  display: block;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -10;
  background: url(/steakhouse/static/media/contact.5153ca7b.jpg) no-repeat center center;
  background: var(--back-img) no-repeat center center;
  background-size: cover;
}

body {
  height: 100%;
  /* background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed; */
  /* background-color: var(--bg-dark-clr); */
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: "Yeseva One";
  width: 100%;
  color: var(--s-clr);
  font-weight: normal;
}

h1 {
  font-size: clamp(2rem, 6vw, 5rem);
  text-transform: capitalize;
}

h2 {
  font-size: clamp(1rem, 6vw, 3rem);
}

h3 {
  font-size: clamp(1rem, 4vw, 2rem);
}

h4 {
  font-size: 28px;
}

h5 {
  font-size: 22px;
}

article {
  box-sizing: border-box;
}

/*=========================== Header start =======================*/

header {
  background: linear-gradient(
    180deg,
    rgba(30, 26, 35, 0.5) 0%,
    rgb(29, 25, 34) 95%
  );
  padding: 0 0 130px 0;
  box-sizing: border-box;
  width: 100%;
  min-height: 35rem;
}

header Nav {
  margin: 0 auto 150px;
}

header h1 {
  grid-area: "h-para";
  margin: 0;
  line-height: 1.2;
}

.reserv {
  padding: 15px 30px;
  border: none;
  margin: 12px 0 0;
}

/*=========================== Header end =======================*/

/*=========================== body start =======================*/

section {
  background: var(--bg-dark-clr);
  padding: 50px 0 25px 0;
}

.welcome .container {
  display: flex;
}

article {
  width: 50%;
  padding: 20px;
}

article img {
  max-width: 100%;
  width: 450px;
  aspect-ratio: auto 450 / 599;
}

.welcome article:last-of-type {
  display: flex;
  justify-content: center;
  align-items: center;
}

article div {
  display: block;
}

p {
  color: var(--light-clr);
}

/*=========================dishes start===============================*/

.dishes {
  background: var(--bg-light-clr);
}

.dishes h2 {
  margin: 0 0 25px;
}

.dishes main {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 5px 25px;
}

.dishes main article {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
}

.dishes article img {
  width: 100%;
}

.dishes article h4 {
  margin: 16px 0 0;
  text-align: center;
}

/*=========================dishes end===============================*/

.empty {
  top: 0;
  height: 35rem;
  width: 100%;
  background: #1d1922;
  opacity: 0.6;
}

/*=========================footer start===============================*/
.footer {
  padding: 20px 0;
}

.footer article {
  width: 100%;
}

.review {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 0;
}

.review h2 {
  margin: 0;
}

.opnion {
  display: grid;
  grid-template-areas:
    "p p"
    "pic name";
  grid-template-columns: 1fr 6fr;
  grid-gap: 20px;
  padding: 2rem;
}

.opnion p {
  grid-area: p;
  font-size: 1rem;
  line-height: 26px;
}

.opnion img {
  grid-area: pic;
  width: ;
}

.opnion h5 {
  grid-area: name;
  margin: 0;
  display: flex;
  align-items: center;
}

.phone {
  padding: 120px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.phone h3 {
  text-align: center;
}

.btn-st {
  border: 2px solid var(--n-clr);
  margin: 16px 0;
}

.footer .info {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-gap: 50px;
  text-align: center;
  align-items: flex-start;
  box-sizing: border-box;
}

.info img {
  margin: 0 auto;
}

.info h4 {
  text-transform: capitalize;
  margin-top: 0;
}

.info .location h4:last-of-type {
  margin: 50px 0 30px;
}

.info ul {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 25px;
  list-style-type: none;
  color: #fff;
  padding: 0;
  margin: auto;
  width: 45%;
}

.icon {
  color: var(--n-clr);
  font-size: 25px;
  transition: all 0.2s ease;
}

.icon:hover {
  color: var(--s-clr);
}

.container .copy {
  width: 100%;
  font-size: 12px;
  color: rgb(122, 122, 122);
  text-align: center;
  margin: 40px 0 7px 0;
}

.hide {
  display: none;
}

/*=========================footer end===============================*/

/*===========Media query===========*/

@media screen and (max-width: 1200px) {
  .container {
    padding: 48px;
  }
}

@media screen and (max-width: 600px) {
  .welcome .container {
    flex-direction: column;
  }

  h1 {
    font-size: clamp(2rem, 10vw, 5rem);
  }

  h2 {
    font-size: clamp(1.8rem, 8vw, 3rem);
  }
  h3 {
    font-size: clamp(1rem, 6vw, 3rem);
  }
  article {
    width: 100%;
    box-sizing: border-box;
  }

  .dishes main {
    box-sizing: border-box;
    overflow: hidden;
    grid-template-columns: 1fr;
  }

  .footer .review {
    grid-template-columns: 1fr;
  }

  .footer .review section:last-of-type {
    padding-top: 0;
  }

  .footer .info {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 400px) {
  .container {
    padding: 12px;
  }

  .welcome .container article {
    padding: 0;
  }

  .footer .review .opnion {
    grid-template-areas: "p p" "pic pic" "name name";
  }
}

.about .container {
  display: grid;
  grid-template-columns: 2fr 0.1fr 4fr;
  grid-template-areas:
    "h h h"
    "img img img"
    "side . article";
  grid-gap: 0 5px;
}

.about h4 {
  grid-area: h;
  text-align: center;
}

.about img {
  grid-area: img;
  max-width: 100%;
  padding: 50px;
  box-sizing: border-box;
}

.about h2 {
  grid-area: side;
}

.about article {
  width: 100%;
  grid-area: article;
  flex-direction: column;
  justify-content: flex-start;
}

.about article h4 {
  text-align: left;
  margin: 12px 0;
}

.about article p {
  line-height: 28px;
}

@media screen and (max-width: 600px) {
  .about .container {
    grid-template-columns: 1fr;
    grid-template-areas:
      "h"
      "img"
      "side"
      "article";
  }
  .about h2 {
    text-align: center;
  }

  .about h4 {
    grid-area: h;
    text-align: center;
    font-size: clamp(1rem, 5vw, 1.5rem);
  }
}

.recipe {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.odd {
  direction: rtl;
}

.recipe img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.recipe article {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  text-align: center;
  box-sizing: border-box;
  align-items: center;
}

.recipe h2,
.recipe h5 {
  margin: auto 0 20px;
}

.recipe p {
  margin: 30px 0 80px;
  width: 100%;
}

@media screen and (max-width: 600px) {
  .recipe {
    grid-template-columns: 1fr;
  }

  .recipe .txt {
    padding: 48px;
  }

  .recipe img {
    min-height: 100vw;
  }

  .odd {
    direction: ltr;
  }
}

.contact .container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas:
    "h2 h2"
    "h3 h3"
    "p p"
    "map form";
  grid-gap: 20px 20px;
  text-align: center;
}

.contact h2 {
  grid-area: h2;
  margin: 0;
}
.contact h3 {
  grid-area: h3;
  margin: 0;
}
.contact p {
  grid-area: p;
  margin-top: 0;
}

.contact article {
  width: 100%;
}

.contact .map {
  grid-area: map;
  height: 100%;
}

.contact form {
  grid-area: form;
  background: var(--bg-light-clr);
  padding: 20px;
  box-sizing: border-box;
  height: auto;
}

.contact form input,
.contact textarea {
  width: 100%;
  font-size: 1rem;
  padding: 0.75em;
  box-sizing: border-box;
  margin: 10px 0;
  border: 1px solid #eaeaea;
  border-radius: 2px;
  background: #fafafa;
  box-shadow: none;
  transition: all 0.2s ease;
}

.contact form h2 {
  text-align: left;
  margin: 20px 0;
}

.contact form .name {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.contact .name input {
  width: 48%;
}

.contact textarea {
  height: 120px;
}

.contact form .submit {
  height: auto;
  float: left;
}

@media screen and (max-width: 600px) {
  .contact .container {
    grid-template-areas:
      "h2 h2"
      "h3 h3"
      "p p"
      "map map"
      "form form";
  }
}

@media screen and (max-width: 400px) {
  .contact form .name {
    flex-direction: column;
  }
  .contact .name input {
    width: 100%;
  }
}

